.App {
    background-image: linear-gradient(-190deg, white, #f6e1e5);
    min-height: 100vh;
}

header.header {
    background-color: #333;
    color: white;
    padding: 1rem;
    margin: 0;
}

.brand {
    text-transform: uppercase;
    text-shadow: 2px 2px 2px #333;
    line-height: 2em;
    font-weight: bold;
}

.brand a {
    text-decoration: none;
    color: white;
}

.brand:first-letter {
    color: red;
    font-size: 2rem;
}

.content {
    margin: 2rem 0 20rem;
}

textarea#add-text,
button#add-text-btn {
    margin: 1rem 0;
    padding: 1rem;
    border: none;
    border-bottom: hotpink solid thick;
    border-radius: 4px;
}

button#add-text-btn {
    cursor: pointer;
}

button#add-text-btn:hover {
    border-bottom-color: deeppink;
    background-color: grey;
}

button#add-text-btn:active {
    border-bottom-color: red;
    background-color: lightgrey;

}

.stamps {
    margin: 2rem 0;
}

.stamps .ts {
    border-bottom: deeppink dotted 1px;
    font-size: 0.7em;
    display: inline-block;
}

.stamps .note {
    white-space: pre-wrap;
    margin: 1rem 0 3rem;
}

footer {
    position: fixed;
    bottom: 0;
    background-color: black;
    color: white;
    display: block;
    margin-top: 2rem;
    min-height: 100px;
    padding: 1rem;
}

footer a {
    color: white;
    font-weight: bold;
}

/* Extracted widths */

@media screen and (min-width: 601px) {
    textarea#add-text,
    button#add-text-btn {
        width: 30%;
        display: inline-block;
        margin: 1rem;
        vertical-align: bottom;
    }

    button#add-text-btn {
        width: 100px;
    }

    .stamps {
        margin: 1rem;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
    }

    footer {
        width: 100%;
    }
}

@media screen and (min-width: 1201px) {
    .stamps {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 600px) {
    textarea#add-text,
    button#add-text-btn {
        width: 70%;
        display: block;
        margin: 1rem;
    }

    .stamps {
        margin: 1rem;
    }

    footer {
        width: 600px;
    }
}

@media screen and (max-width: 400px) {
    textarea#add-text,
    button#add-text-btn {
        width: calc(100% - 2rem);
    }

    footer {
        width: 400px;
    }
}

@media screen and (max-width: 300px) {

    textarea#add-text,
    button#add-text-btn {
        width: calc(100% - 2rem);
    }

    footer {
        width: 300px;
    }
}

