body {
    padding: 0;
    margin: 0;
}

*, *::after, *::before {
    box-sizing: border-box;
}

@media screen and (max-width: 800px) {

    /* screen and mobile max-width: 800px */
    body {
        width: 800px;
    }
}

@media screen and (max-width: 600px) {

    /* screen and mobile max-width: 600px */
    body {
        width: 600px;
    }
}

@media screen and (max-width: 400px) {

    /* screen and mobile max-width: 400px */
    body {
        width: 400px;
    }
}

@media screen and (max-width: 300px) {

    /* screen and mobile max-width: 300px */
    body {
        width: 300px;
    }
}






